 import * as Actions from '../Actions/ActionTypes';

 const initialState = {
     apiStatus: {
         type: undefined,
         message: undefined
     },
 }
 
 export const APIReducer = (state = initialState, action: any) => {
     switch (action.type) {
         case Actions.API_STATUS:
             return Object.assign({}, state, {
                 apiStatus: action.apiStatus
             })
         default:
             return state;
     }
 }
 