import React, { useEffect } from 'react';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert, { AlertProps } from '@material-ui/lab/Alert';
import { makeStyles, Theme } from '@material-ui/core/styles';
import { connect } from 'react-redux';
import { updateNotofication } from '../../Actions/ActionTypes';

function Alert(props: AlertProps) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = makeStyles((theme: Theme) => ({
    root: {
        width: '100%',
        '& > * + *': {
            marginTop: theme.spacing(2),
        },
    },
}));

function NotificationPopup(props: any) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(true);

    useEffect(
        () => {
            setOpen(true);
        },
        [props.apiStatus.type]
    );

    const handleClose = (event?: React.SyntheticEvent, reason?: string) => {
        updateNotofication({ type: undefined, message: undefined })
        if (reason === 'clickaway') {
            return;
        }
        setOpen(false);
    };

    if (props.apiStatus.type === undefined) {
        return null
    }

    return (
        <div className={classes.root}>
            <Snackbar
                open={open}
                autoHideDuration={3000}
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'center',
                }}
                onClose={handleClose}>
                <Alert onClose={handleClose} severity={props.apiStatus.type}>
                    {props.apiStatus.message}
                </Alert>
            </Snackbar>
        </div>
    );
}

const mapStateToProps = (state: any) => {
    return {
        apiStatus: state.APIReducer.apiStatus
    }
}

export default connect(mapStateToProps)(NotificationPopup);