import React from 'react';
import { Table, TableBody, TableRow, TableCell } from "@material-ui/core";
import { KeyLabelMappint } from '../../services/Constants';
import { traverseValue } from '../../services/util';
import Rupee from '../RuppeSign';

export default function InfoTable(item: any) {
    return <Table aria-label="simple table">
        <TableBody>
            {Object.keys(item).filter((key: any) => KeyLabelMappint[key] !== undefined).map((key: any) => {
                return (
                    <TableRow key={key} >
                        <TableCell>
                            {KeyLabelMappint[key]}
                        </TableCell>
                        <TableCell align="left">
                            {key === 'price' ? <Rupee /> : ''}
                            <b>{traverseValue(key, item)}</b>
                        </TableCell>
                    </TableRow>
                );
            })}
        </TableBody>
    </Table>
}