import React, { useState } from 'react';
import { ListItem, ListItemText, ListItemSecondaryAction, Paper, Button, Typography, DialogContentText, ListItemAvatar } from '@material-ui/core';
import InfoDialog from '../Dialog/InfoDialog';
import InfoTable from '../Table/InfoTable';
import { CANCELLED_STATUS } from '../../services/Constants';
import { call } from '../../services/Api';
import { updateNotofication } from '../../Actions/ActionTypes';
import NotificationsIcon from '@material-ui/icons/Notifications';
import { COLORS } from '../../assets/style';
import CancelIcon from '@material-ui/icons/Cancel';

export default function CustomListItem(props: any) {
    const [isApiCallInProgress, setIsApiCallInProgress] = useState(false);
    const [errorMessage, serErrorMessage] = useState('');
    const [cancelDialogOpen, setCancelDialogOpen] = useState(false);

    const createInfoComponent = (item: any) => {
        return <InfoTable {...item} />
    }

    const cancelAppointment = (id: string) => {
        setIsApiCallInProgress(true);
        call(`appointment/cancel/${id}`, 'PUT').then((response: any) => {
            setIsApiCallInProgress(false);
            if (response.error) {
                serErrorMessage(response.error)
            } else if (response.deletedCount === 0) {
                serErrorMessage(`Can Not Cancel ${id}`)
            } else {
                serErrorMessage('');
                updateNotofication({ type: "info", message: `${id} Canceled` });
                setCancelDialogOpen(false);
                props.cb();
            }
        })
    }

    const createCancelAppointmentDialog = (appointmentDetails: any) => {
        return <div className="text-center">
            <DialogContentText className="text-left">
                Are you sure you want to cancel {appointmentDetails.id}
            </DialogContentText>
            <Button
                className="mt-2"
                variant="outlined"
                disabled={isApiCallInProgress}
                onClick={() => { cancelAppointment(appointmentDetails.id) }}
                color="secondary"
                autoFocus>
                {isApiCallInProgress ? "Canceling..." : (errorMessage ? "Retry" : "Yes")}
            </Button>
        </div>
    }

    return <Paper elevation={2} className={`${props.past ? 'bg-light' : ''}`}>
        <ListItem className="mb-2" style={{ borderLeft: `3px solid ${COLORS.app_orange}` }}>
            <ListItemAvatar className="pr-2 border-right">
                <>
                    <span>{props.time}</span>
                    <br />
                    <span>{props.date}</span>
                </>
            </ListItemAvatar>
            <ListItemText className="ml-2" primary={props.service}
                secondary={
                    <>
                        <span className="d-flex flex-row">
                            <InfoDialog
                                btnVariant="text"
                                btnLabel={props.details.name}
                                title={props.service}
                                component={createInfoComponent(props.details)} />
                        </span>
                    </>} />
            <ListItemSecondaryAction className="d-flex flex-row align-items-center">
                <InfoDialog
                    isIconButton={true}
                    iconButton={<NotificationsIcon />}
                    title="Remarks"
                    component={<Typography>{props.remarks}</Typography>}
                    isDisabled={!props.remarks}
                />

                {props.status === CANCELLED_STATUS ? <Typography variant="button" color="error">{props.status}</Typography>
                    :
                    !props.past && <InfoDialog
                        open={cancelDialogOpen}
                        setDialogOpen={setCancelDialogOpen}
                        isIconButton={true}
                        iconButton={<CancelIcon color="error" />}
                        title="Cancel Appointment"
                        component={createCancelAppointmentDialog(props)}
                    />}

            </ListItemSecondaryAction>
        </ListItem>
    </Paper>
}